
.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  /* height: 100vh; */
  margin: 0;
  background-color: rgb(255, 255, 255);
}

.input-form {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
}

.input-form label {
  margin: 0 10px;
  color: rgb(0, 0, 0);
}

input {
  background-color: rgb(255, 255, 255);
  border: 1px solid rgb(0, 0, 0);
  border-radius: 4px;
  color: rgb(0, 0, 0);
  padding: 4px 6px;
  margin-left: 4px;
}

canvas {
  display: block;
}
